'use client'; // 声明这是一个客户端组件
import React from'react';
import { Text } from '@/components/ui/text';
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from '@/components/ui/input';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import { AiOutlineIdcard } from "react-icons/ai";
import ProtectedRoute from '@/components/ProtectedRoute'
import api from '@/lib/api'; // 导入API实例
import '@/app/globals.css';

export default function HomePage() {
  const router = useRouter();
  const { isAuthenticated, logout } = useAuth();
  const [username, setUsername] = useState('');
  const [loading, setLoading] = useState(true); // 新增loading状态

  // 页面保护：未登录直接跳转
  useEffect(() => {
    if (!isAuthenticated) {
      router.replace('/login'); // 更推荐replace，防止回退
    }
  }, [isAuthenticated, router]);

  // 拉取用户信息
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await api.get('/api/user/me');
        setUsername(response.data.username);
      } catch (error) {
        console.error('获取用户信息失败');
        setUsername('未知用户');
      } finally {
        setLoading(false);
      }
    };
    if (isAuthenticated) {
      fetchUser();
    }
  }, [isAuthenticated]);

  const handleBack = () => {
    window.history.back();
  };

  // 页面保护和加载状态
  if (!isAuthenticated || loading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  return (
    <ProtectedRoute>
      <div className="flex justify-between items-center p-4 bg-gray-100 shadow">
          <div className="flex items-center bg-gray-100">
          <Button variant="ghost" className="mr-2">
              AI+协同文献管理平台
          </Button>
          </div>
          <div className="flex-1">
          <Input
              placeholder="搜索文献..."
              className="w-full bg-white"
          />
          </div>
          <div className="flex items-center">
            {/* 用户名按钮，点击跳转到个人主页 */}
            <Button
              variant="ghost"
              className="mr-2"
              onClick={() => router.push('/main')}
              title="点击返回上一页"
            >
              {username}
            </Button>
            {/* 显示登录状态和退出按钮 */}
            {isAuthenticated && (
              <Button variant="outline" size="sm" onClick={logout}>
                退出登录
              </Button>
            )}
          </div>
      </div>
      <div className="flex justify-center items-center h-screen bg-white px-4 sm:px-8 relative">
        <Card className="w-full max-w-none p-6 bg-white shadow-md rounded-lg h-100%">
          <div className="flex items-center mb-4">
            <AiOutlineIdcard
              className="w-16 h-16 rounded-full mr-4"
            />
            <div>
              <Text as="h2" className="text-3xl font-bold">{username}</Text>
              <Text as="p" className="text-xl text-gray-600">机器学习课题组</Text>
            </div>
          </div>
          <div className="flex justify-around flex-1 mt-8">
            {/* 私人文献 */}
            <div className="flex flex-col items-center">
              <Button className="mb-10 -mt-9 text-xl py-6 px-8">创建课题组</Button>
              <Text as="h3" className="text-2xl font-bold">28</Text>
              <Text as="p" className="text-gray-600 md-8">私人文献</Text>
            </div>
            {/* 公共文献 */}
            <div className="flex flex-col items-center">
              <Button variant="outline" className="mb-10 -mt-9 text-xl py-6 px-8">加入课题组</Button>
              <Text as="h3" className="text-2xl font-bold">156</Text>
              <Text as="p" className="text-gray-600 md-8">公共文献</Text>
            </div>
            {/* 最近上传 */}
            <div className="flex flex-col items-center">
              <Button variant="destructive" className="mb-10 -mt-9 text-xl py-6 px-8">退出课题组</Button>
              <Text as="h3" className="text-2xl font-bold">12</Text>
              <Text as="p" className="text-gray-600 md-8">最近上传</Text>
            </div>
          </div>
        </Card>
      </div>
    </ProtectedRoute>
  );
}